.buttons-container {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin: 10px 0;
  }
  
  img {
    width: 100%;
  }

  .coverSize{
    width: 25rem;
    height: 25rem;
  }

  .textPosition{
    margin-left: 10rem;
  }

  #albumList {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 15px;
  }

  